.root {
  display: grid;
  grid-template-columns: 1.5fr 8.5fr;
  width: 80%;
  margin: 0 auto;
  min-height: 100vh;
}

/* base */
li {
  list-style: none;
}
a {
  text-decoration: none;
}

/* 240821 추가 */
.imgtd {
  width: 150px;
}

img {
  border: none;
}
textarea {
  resize: none;
}
select,
option {
  font-size: 15px;
}

